<template>
  <div class="notLogin" v-if="!loginState">
    <login_card
      v-for="(item, index) in coffee_shops"
      :key="index"
      :title="item.name"
      :loginType="item.loginType"
      @login="login"
    ></login_card>

    <carrier_card></carrier_card>

    <div class="link_button_container">
      <link_button
        v-for="(item, index) in button_array"
        :key="index"
        :title="item.name"
        :icon="item.icon"
      ></link_button>
    </div>
  </div>
  <div v-else>
    <vip_card :userName="getUserName" :loginType="getShopType" :avatar="ironman"></vip_card>
    <carrier_card></carrier_card>
  </div>
</template>

<script>
import axios from "axios";
import ironman from './assets/ironman.png';
import member from "./assets/member.png";
import login_card from "./components/login_card.vue";
import carrier_card from "./components/carrier_card.vue";
import link_button from "./components/link_button.vue";
import vip_card from "./components/vip_card.vue";
export default {
  components: {
    login_card,
    carrier_card,
    link_button,
    vip_card
  },
  beforeMount() {
    this.handleLoginCallback();
  },
  computed: {
    loginState() {
      return this.$store.state.isLogin;
    },
    getUserName() {
      return this.$store.state.userName;
    },
    getShopType() {
      return this.$store.state.shopType;
    },
    
  },
  mounted() {
    // if (this.loginState && this.getCarrier != "") {
    //   this.createCarrierCode();
    // }
  },
  data() {
    return {
      ironman: ironman,
      coffee_shops: [
        {
          name: "Bryant咖啡館",
          loginType: "Bryant",
        },
        {
          name: "cama coffee",
          loginType: "cama",
        },
        {
          name: "咖啡深藏",
          loginType: "soul",
        },
      ],
      button_array: [
        {
          icon: member,
          name: "會員",
        },
        {
          icon: member,
          name: "優惠卷",
        },
        {
          icon: member,
          name: "積點",
        },
        {
          icon: member,
          name: "價格",
        },
        {
          icon: member,
          name: "集點卡",
        },
        {
          icon: member,
          name: "抽獎",
        },
        {
          icon: member,
          name: "寄存卡",
        },
        {
          icon: member,
          name: "行銷活動",
        },
        {
          icon: member,
          name: "儲值信託",
        },
        {
          icon: member,
          name: "履約票卷",
        },
      ],
    };
  },
  methods: {
    // createCarrierCode() {
    //   new JsBarcode("#barcode", this.getCarrier, {
    //     format: "CODE128",
    //     displayValue: true,
    //     fontSize: 18,
    //     height: 50,
    //     width: 2,
    //     textMargin: 0,
    //     margin: 0,
    //   });
    // },
    login(routerType) {
      let URL = "https://access.line.me/oauth2/v2.1/authorize?";
      URL += "response_type=code";
      URL += "&client_id=1661441879";
      URL += "&redirect_uri=https://qkqp3qr2.github.io/blankvue/";
      URL += `&state=${routerType}`;
      URL += "&scope=openid%20profile";
      window.location.href = URL;
      console.log("start login");
    },
    buildUrl(routerType) {
      let baseUrl = "https://access.line.me/oauth2/v2.1/authorize?";
      const params = {
        response_type: "code",
        client_id: "1661441879",
        redirect_uri: "https://qkqp3qr2.github.io/blankvue/",
        state: "1",
        scope: "openid%20profile",
      };

      params.shop = routerType;
      const queryString = Object.entries(params)
        .map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
        .join("&");

      return `${baseUrl}${queryString}`;
    },
    async handleLoginCallback() {
      const code = new URLSearchParams(window.location.search).get("code");
      if (code) {
        const accessToken = await this.getAccessToken(code);
        await this.getUserProfile(accessToken);
        await this.checkShopType();
        this.$store.commit("setLoginState");
        console.log("login successful");
      } else {
        console.error("Authorization code not found.");
      }
    },
    async getAccessToken(code) {
      try {
        const response = await axios.post(
          "https://api.line.me/oauth2/v2.1/token",
          new URLSearchParams({
            grant_type: "authorization_code",
            code,
            client_id: "1661441879",
            client_secret: "4f03d9065b14f7d6e394a0f20b0c53e3",
            redirect_uri: "https://qkqp3qr2.github.io/blankvue/",
          }).toString(),
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
            },
          }
        );

        return response.data.access_token;
      } catch (error) {
        console.error("Failed to get access token:", error);
        throw error;
      }
    },
    async getUserProfile(accessToken) {
      try {
        const response = await axios.get("https://api.line.me/v2/profile", {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        });

        const userProfile = response.data;
        console.log(userProfile);
        this.$store.commit("setUserName", userProfile.displayName);
      } catch (error) {
        console.error("Failed to get user profile:", error);
      }
    },
    async checkShopType() {
      const shop = new URLSearchParams(window.location.search).get("state");
      if (shop) {
        this.$store.commit("setShopType", shop);
      } else {
        this.$store.commit("setShopType", "error");
      }
    },
  },
};
</script>

