import { createStore } from "vuex";

const store = createStore({
  state() {
    return {
      isLogin: false,
      userName: "",
      shopType: "",
      carrier: "/CYJJDGG",
      member_qrCode: "test_qrCode",
    };
  },
  mutations: {
    setLoginState(state) {
      state.isLogin = true;
    },
    setUserName(state, name) {
      state.userName = name;
    },
    setShopType(state, type) {
      state.shopType = type;
    },
    setCarrier(state, carrier) {
      state.carrier = carrier;
    },
  },
});

export default store;
