<template>
  <div class="code_card" v-if="!loginState">
    <div class="buttons_container">
      <div
        v-for="(item, id) in tap_buttons"
        :key="id"
      >
        <p>{{ item.name }}</p>
      </div>
    </div>
    <div class="to_login_hint">
      <p>請用 <span style="color: green">LINE</span> 登入</p>
    </div>
    <div class="to_login_hint">
      <p>免費領取VIP會員卡</p>
    </div>
  </div>
  <div class="code_card" v-else>
    <div class="buttons_container">
      <button
        v-for="(item, id) in tap_buttons"
        :key="id"
        :class="tabid == id ? 'tabed' : 'tab'"
        @click="changeTab(id)"
      >
        <p>{{ item.name }}</p>
      </button>
    </div>

    <QRCode :value="getQrcode" :size="100" level="H" v-show="tabid == 0"/>
    <svg id="barcode" v-show="tabid == 1"></svg>
  </div>
</template>
<script>
import JsBarcode from "jsbarcode";
import QRCode from "qrcode.vue";
export default {
  props: {},
  components: {
    QRCode,
  },
  data() {
    return {
      tabid: "0",
      tap_buttons: [
        {
          name: "會員QR碼",
          id: "0",
        },
        {
          name: "手機載具",
          id: "1",
        },
      ],
    };
  },
  computed: {
    loginState() {
      return this.$store.state.isLogin;
    },
    getCarrier() {
      return this.$store.state.carrier;
    },
    getQrcode() {
      return this.$store.state.member_qrCode;
    },
  },
  methods: {
    getBarcode() {
      new JsBarcode("#barcode", this.getCarrier, {
        format: "CODE128",
        displayValue: true,
        fontSize: 18,
        height: 50,
        width: 2,
        textMargin: 0,
        margin: 0,
        lineColor: "#999",
      });
    },
    changeTab(id) {
      this.tabid = id;
      if (id == 1) {
        this.getBarcode();
      }
    },
  },
};
</script>
